// 
export function getFramesSeats({ dispatch, state }, newVal, name) {
    this.$http({
        url: '/device/idc_query/?id=' + newVal + '&type=' + name,
        method: 'GET'
    })
    .then(response => {
        if (response.data.code === 200) {
            name === 'room' ? dispatch('GETFRAMES', response.data) : dispatch('GETSEATS', response.data)
        } 
    })
}

