export function getOrigins({ dispatch, state }, newVal) {
    this.$http({
        url: '/device/source_query/?id=' + newVal,
        method: 'GET'
    })
    .then(response => {
        if (response.data.code === 200) {
           dispatch('GETORIGINS', response.data)
        } 
    })
}

